#header{
    width: 100%;
    height: 100vh;
    background-image: url(backgroundd.png);
    background-size: cover;
    background-position: center;
}

.container{
    padding: 10px 10%;
}

nav{
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
}

nav ul li {
    display: inline-block;
    list-style: none;
    margin: 10px 20px;
}

nav ul li a {
    color: #fff;
    text-decoration: none;
    font-size: 18px;
    position: relative;
}

nav ul li a::after{
    content: '';
    width: 0;
    height: 3px;
    background: #ff004f;
    position: absolute;
    left: 0;
    bottom: -6px;
    transition: 0.5s;
}

nav ul li a:hover::after{
    width: 100%;

}

.title{
    color: #fff;
    text-decoration: underline;
    font-size: 50px;
}

.header-text{
    margin-top: 20%;
    font-size: 30px;
    color: #fff;
}

.header-text h1{
    font-size: 50px;
    margin-top: 20px;
}

.header-text h1 span {
    color: red;
}

nav #xmark{
    display: none;
}

#sidemenu {
    right: -200px; /* Start hidden */
    top: 0; /* Align to the top */
    width: 200px; /* Set desired width */
    height: 100%; /* Full height */
    transition: right 0.3s ease; /* Smooth transition */
    /* Add background color, etc. */
    background-color: white; /* Example background */
}

@media only screen and (max-width:600px){
    #header{
        background-image: url(phone.png);
    }
    .header-text{
        margin-top: 30%;
        font-size: 16px;
    }
    .header-text h1{
        font-size: 20px;
    }
    nav .fa-solid{
        display: block;
        font-size: 25px
    }
    nav ul{
        background: #ff004f;
        position: fixed;
        top: 0;
        right: -200px;
        width: 200px;
        height: 100vh;
        padding-top: 50px;
        z-index: 2;
        transition: right 0.5s;
    }
    nav ul li{
        display: block;
        margin: 25px;
    }
    nav ul .fa-solid{
        position: absolute;
        top: 25px;
        left: 25px;
        cursor: pointer;
    }
}