#Hero {
    padding: 80px 0;
    color: #ababaa;
    display: flex;
}

.row {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}

.Hero-col-1{
    flex-basis: 35%;
}

.Hero-col-1 img{
    width: 100%;
    border-radius: 20px;
}

.Hero-col-2{
    flex-basis: 60%;
}

.sub-title {
    font-size: 60px;
    font-weight: 600;
    color: #fff;
}

.tab-titles{
    display: flex;
    margin: 20px 0 40;
}

.tab-links{
    margin-right: 50px;
    font-size: 18px;
    font-weight: 500;
    cursor: pointer;
    position: relative;
}

.tab-links::after{
    content: '';
    width: 0;
    height: 3px;
    background: #ff004f;
    position: absolute;
    left: 0;
    bottom: -8px;
    transition: 0.5s;
}

.tab-links.active-link::after{
    width: 50%;
}

.tab-contents ul li{
    list-style: none;
    margin: 10px 0;
}

.tab-contents ul li span{
    color: #b54769;
    font-size: 14px;
}

.tab-contents{
    display: none;
}

.tab-contents.active-tab{
    display: block;
}

@media only screen and (max-width:600px){
    .sub-title{
        font-size: 40px;
    }

    .about-col-1, .about-col-2{
        flex-basis: 100%;
    }
    .about-col-1{
        margin-bottom: 30px;
    }
    .about-col-2{
        font-size: 14px;
    }

    .Hero-col-1{
        flex-basis: 35%;
    }
    
    .Hero-col-1 img{
        width: 400px;
        border-radius: 20px;
    }
    
    .Hero-col-2{
        flex-basis: 100%;
    }
}